import React from "react";

const CaptionedImage = ({ image, caption, alt, maxHeight}) => {
    if(alt === undefined){
        alt = caption
    }

    var pb;
    if (caption === undefined) pb = ''
    else pb = ' pb-0'
    

    var heightSpec = ''
    if(maxHeight !== undefined){
        heightSpec = ` max-h-${maxHeight}`
    }
    
    return (
        <div class={`flex flex-col max-w-md items-center p-2${pb}`}>
            <div class="flex w-96 justify-center">
                <img src={image} class={`object-contain border-amber-500 rounded-md border-b-2
                ${heightSpec}`} alt={alt}></img>
            </div>
            <div className="flex rounded-b-lg">
                <p class={"ml-0 mt-0 mb-0 text-sm pl-1 pr-1 bg-amber-500 rounded-b-sm"}>{caption}</p>
            </div>
        </div>
    );
};

export default CaptionedImage;